import Home from "./Home";
import Covidcorner from "./Covidcorner";
import { Switch, Route } from "react-router-dom";
import Heading from "./Heading";
import DailySearch from "./DailySearch";
import Quizz from "./Quizz";

export default function App() {
  return (
    <div>
      <Heading />
      <Switch>
        <Route exact path="/search" component={Home} />
        <Route exact path="/covid" component={Covidcorner} />
        <Route exact path="/" component={Covidcorner} />
        <Route exact path="/search/date" component={DailySearch} />
        <Route path="/quiz" component={Quizz} />
      </Switch>
    </div>
  );
}
