import React, { useState, useEffect } from "react";
import { Card, Container, Form } from "react-bootstrap";

export default function Home() {
  const [mainData, setMainData] = useState([]);
  const [search, setSearch] = useState("");
  const [totalData, setTotalData] = useState([]);

  useEffect(() => {
    async function Hell() {
      const response = await fetch("https://data.covid19india.org/data.json");
      const data = await response.json();
      setMainData(
        data.statewise.filter(
          (k) => k.state !== "Total" && k.state !== "State Unassigned"
        )
      );
    }
    Hell();
  }, []);

  useEffect(() => {
    if (search === "") {
      return setTotalData([]);
    } else {
      const find = mainData.filter((kk) =>
        kk.state.toLowerCase().includes(search.toLowerCase())
      );

      return setTotalData(find);
    }
  }, [search, mainData]);

  const searchingData = (cases) => {
    return (
      <div>
        <Container className="mt-4 h-auto text-center">
          <Card>
            <Card.Header style={{ backgroundColor: "#c8d8e4" }}>
              <b>
                <h3>{cases.state}</h3>
              </b>
            </Card.Header>
            <Card.Body style={{ backgroundColor: "#f2f2f2", color: "#2b6777" }}>
              <Card.Text>
                <h6>Confirmed : {cases.confirmed}</h6>
              </Card.Text>
              <Card.Text>
                <h6>Active : {cases.active} </h6>
              </Card.Text>
              <Card.Text>
                <h6>Recovered: {cases.recovered} </h6>
              </Card.Text>
              <Card.Text>
                <h6>Deaths : {cases.deaths} </h6>
              </Card.Text>
            </Card.Body>
          </Card>
        </Container>
      </div>
    );
  };

  return (
    <>
      <Container className="mt-5 mb-4">
        <h5 className="heading_styles">Search Statewise :</h5>
      </Container>
      <div>
        <Form className="w-50 mr-auto ml-auto">
          <Form.Control
            type="text"
            value={search}
            placeholder="Enter a State / UT"
            onChange={(s) => setSearch(s.target.value)}
          />
          <br />
          <br />
        </Form>

        {totalData.map(searchingData)}
      </div>
    </>
  );
}
