import React, { useEffect, useState } from "react";
import { Table, Container, Row, Col, Card } from "react-bootstrap";
import CountUp from "react-countup";
import Footer from "./Footer";
import Charts from "./Charts";

export default function Covidcorner() {
  const [data, setData] = useState([]);
  const [active, setActive] = useState([]);
  const [confirmed, setConfirmed] = useState([]);
  const [deaths, setDeaths] = useState([]);
  const [recovered, setRecovered] = useState([]);

  useEffect(() => {
    async function Hel() {
      const response = await fetch("https://data.covid19india.org/data.json");
      const data = await response.json();
      console.log(data);
      setActive(data.statewise[0].active);
      setConfirmed(data.statewise[0].confirmed);
      setDeaths(data.statewise[0].deaths);
      setRecovered(data.statewise[0].recovered);
      setData(
        data.statewise.filter(
          (k) => k.state !== "Total" && k.state !== "State Unassigned"
        )
      );
    }
    Hel();
  }, []);

  return (
    <div style={{ marginTop: "35px" }}>
      <Container>
        <h5 className="heading_styles">Total Cases Count :</h5>
      </Container>
      <Container className="text-center mb-3">
        <Row>
          <Col className="p-2 rounded mr-2 text-danger">
            <Card bg="text-center">
              <Card.Body>
                <Card.Text>
                  <h2 className="rotate">
                    <i className="fas fa-viruses"></i>
                  </h2>
                </Card.Text>

                <Card.Text>
                  <b>Active</b>
                </Card.Text>
                <Card.Text>
                  <CountUp end={active} duration={7} />
                </Card.Text>
              </Card.Body>
            </Card>
          </Col>
          <Col className="p-2 rounded ml-2 text-info">
            <Card bg="text-center">
              <Card.Body>
                <Card.Text>
                  <h2 className="rotate">
                    <i class="fab fa-creative-commons-sampling-plus"></i>
                  </h2>
                </Card.Text>

                <Card.Text>
                  <b>Confirmed</b>
                </Card.Text>

                <Card.Text>
                  <CountUp end={confirmed} duration={5} />
                </Card.Text>
              </Card.Body>
            </Card>
          </Col>

          <Col className="p-2 rounded mr-2 text-secondary">
            <Card bg="text-center">
              <Card.Body>
                <Card.Text>
                  <h2 className="rotate">
                    <i class="fas fa-book-dead"></i>
                  </h2>
                </Card.Text>

                <Card.Text>Deaths</Card.Text>
                <Card.Text>
                  <CountUp end={deaths} duration={4} />
                </Card.Text>
              </Card.Body>
            </Card>
          </Col>
          <Col className="p-2 rounded ml-2 text-success">
            <Card bg="text-center">
              <Card.Body>
                <Card.Text>
                  <h2 className="rotate">
                    <i class="fas fa-user-shield"></i>
                  </h2>
                </Card.Text>

                <Card.Text>
                  <b>Recovered</b>
                </Card.Text>
                <Card.Text>
                  <CountUp end={recovered} duration={10} />
                </Card.Text>
              </Card.Body>
            </Card>
          </Col>
        </Row>
      </Container>

      <Container>
        <h5 className="heading_styles" style={{ marginBottom: "25px" }}>
          State Wise Cases :
        </h5>
        <Table striped hover bordered responsive>
          <thead>
            <tr>
              <th>State</th>
              <th>Confirmed</th>
              <th>Active</th>
              <th>Recovered</th>
              <th>Deceased</th>
            </tr>
          </thead>
          <tbody>
            {data.map((k) => (
              <tr>
                <td>{k.state}</td>
                <td>{k.confirmed}</td>
                <td>{k.active}</td>
                <td>{k.recovered}</td>
                <td>{k.deaths}</td>
              </tr>
            ))}
          </tbody>
        </Table>
      </Container>
      <Charts />
      <Footer />
    </div>
  );
}
