import React, { useEffect, useState } from "react";
import { Container, Form } from "react-bootstrap";
import { Bar } from "react-chartjs-2";

export default function Charts() {
  const [DailyCon, setDailyCon] = useState([]);
  const [main, setMain] = useState([]);
  const [DailyDec, setDailyDec] = useState([]);
  const [DailyRec, setDailyRec] = useState([]);
  const [value, setValue] = useState(7);

  useEffect(() => {
    async function Hell() {
      const response = await fetch("https://data.covid19india.org/data.json");
      const data = await response.json();
      setDailyCon(data.cases_time_series.map((a) => a.dailyconfirmed));
      setMain(data.cases_time_series.map((a) => a.dateymd));
      setDailyDec(data.cases_time_series.map((a) => a.dailydeceased));
      setDailyRec(data.cases_time_series.map((a) => a.dailyrecovered));
    }
    Hell();
  }, []);

  const hello = {
    labels: main.slice(-value),

    type: "Bar",
    datasets: [
      {
        data: DailyCon.slice(-value),
        backgroundColor: "#ff3333",
        label: "Confirmed"
      },

      {
        data: DailyRec.slice(-value),
        backgroundColor: "#008000",
        label: "Recovered"
      },

      {
        data: DailyDec.slice(-value),
        backgroundColor: "#000000",
        label: "Deceased"
      }
    ]
  };

  const chartOptions = {
    responsive: true,
    maintainAspectRatio: false
  };

  return (
    <div>
      <Container className="mt-4">
        <h5 className="heading_styles"> India Spread Trends :</h5>
      </Container>
      <Container className="mt-4 mb-5">
        <div style={{ width: "120px" }} className="ml-auto">
          <Form.Control as="select" onChange={(e) => setValue(e.target.value)}>
            <option value="2">2 Days</option>
            <option value="7">7 Days</option>
            <option selected value="14">
              14 Days
            </option>
          </Form.Control>
        </div>
      </Container>
      <Container className="mt-4" style={{ height: "500px" }}>
        <Bar data={hello} options={chartOptions}></Bar>
      </Container>
    </div>
  );
}
