import React, { useEffect, useState } from "react";
import { Card, Form, Button, Container } from "react-bootstrap";
import "./index.css";

export default function DailySearch() {
  const [search, setSearch] = useState([]);
  const [searchfiled, setSearchFiled] = useState("");
  const [final, setFinal] = useState([]);
  const [datemax, setDateMax] = useState("");

  useEffect(() => {
    async function Hell() {
      const response = await fetch("https://data.covid19india.org/data.json");
      const data = await response.json();
      setSearch(
        data.cases_time_series.filter(
          (k) => k.state !== "Total" && k.state !== "State Unassigned"
        )
      );
      setDateMax(
        data.cases_time_series[data.cases_time_series.length - 1].dateymd
      );
    }
    Hell();
  }, []);

  const kin = () => {
    const searched = search.filter((m) => m.dateymd === searchfiled);
    return setFinal(searched);
  };

  const finalizedData = (k) => {
    return (
      <>
        <Card className="w-75 ml-auto mr-auto">
          <Card.Header style={{ backgroundColor: "#c8d8e4" }}>
            <b>
              <h3>Registered Cases</h3>
            </b>
          </Card.Header>
          <Card.Body style={{ backgroundColor: "#f2f2f2", color: "#2b6777" }}>
            <Card.Text>
              <h5>Dailyconfirmed : {k.dailyconfirmed}</h5>
            </Card.Text>
            <Card.Text>
              <h5>Dailydeceased : {k.dailydeceased}</h5>
            </Card.Text>
            <Card.Text>
              <h5> Dailyrecovered : {k.dailyrecovered}</h5>
            </Card.Text>
          </Card.Body>
        </Card>
      </>
    );
  };

  return (
    <div className="mt-5 text-center">
      <Container>
        <h5 className="mb-4 text-left" style={{ color: "#116880" }}>
          Daily Covid Cases :
        </h5>
      </Container>

      <Form className="w50 mr-auto ml-auto text-center">
        <Form.Control
          type="date"
          onChange={(e) => setSearchFiled(e.target.value)}
          min="2020-01-30"
          max={datemax}
          className="w-50  ml-auto mr-auto"
          required
        />

        <Button
          onClick={kin}
          variant="info"
          className="mt-5 p-2 pl-3 pr-3 mb-5"
          disabled={searchfiled.length === 0}
        >
          Search
        </Button>
      </Form>

      {final.map(finalizedData)}
    </div>
  );
}
