import React, { useState } from "react";
import { Container, Row, Col, Card, Button } from "react-bootstrap";

export default function Quizz() {
  const [change, setChange] = useState(0);
  const [totalCorrect, setTotalCorrect] = useState(0);

  const Acutalans = [
    {
      QuestionText:
        "Currently, which group has had more severe effects of COVID-19?",
      answerOptions: [
        { answerText: "Men", value: false },
        { answerText: "Women", value: true }
      ]
    },

    {
      QuestionText:
        "True or false: COVID-19 has currently been detected on all seven continents.",
      answerOptions: [
        { answerText: "True", value: false },
        { answerText: "False", value: true }
      ]
    },

    {
      QuestionText:
        "True or False: A vaccine stimulates your immune system to produce antibodies, like it would if you were exposed to the virus.",
      answerOptions: [
        { answerText: "True", value: true },
        { answerText: "False", value: false }
      ]
    }
  ];

  const tochange = (vlauepassed) => {
    if (vlauepassed === true) {
      setTotalCorrect(totalCorrect + 1);
    }

    const update = change + 1;

    if (update <= Acutalans.length) return setChange(update);
  };

  const toTakeRetest = () => {
    setChange(0);
    setTotalCorrect(0);
  };

  return (
    <div className="text-center mt-4">
      <Container>
        <div
          className="w-75 h-25"
          style={{
            backgroundColor: "#70b5c9",
            backgroundRepeat: "no-repeat",
            position: "absolute",
            marginTop: "-25px",
            marginLeft: "-15px",
            borderRadius: "0px 0px 200px 15px",
            zIndex: "-1"
          }}
          src="https://www.w3schools.com/images/w3schools_green.jpg"
          alt=""
        />
        <h5 className="text-left">Quizz</h5>
        <div className="d-flex justify-content-end mb-2"></div>
        {Acutalans.length === change ? (
          <div>
            <Card
              className="w-75 ml-auto mr-auto mb-4"
              style={{ height: "200px", borderRadius: "20px 30px 25px 120px" }}
            >
              <Card.Body>
                <Card.Text>
                  <h6 className="text-info mt-4">
                    You have answered <b> {totalCorrect} </b> correct
                  </h6>
                </Card.Text>
                <Card.Text>
                  <p style={{ color: "green", marginTop: "30px" }}>
                    you have scored {(totalCorrect / Acutalans.length) * 100}%
                  </p>
                </Card.Text>
              </Card.Body>
            </Card>
            <Button className="bg-info" onClick={toTakeRetest}>
              retest
            </Button>
          </div>
        ) : (
          <div>
            <Row>
              <Col sm={12} md={6} xl={6} xxl={6}>
                <div>
                  <Card
                    className="p-4 mb-3 mt-3"
                    style={{
                      borderRadius: "15px",
                      boxShadow: "#2b6777  0px 13px 27px -10px"
                    }}
                  >
                    <Card.Body className="p-4 ">
                      <Card.Text className="text-info">
                        Question {change + 1}/{Acutalans.length}
                      </Card.Text>
                      <Card.Text className="text-info">
                        <b>{Acutalans[change].QuestionText}</b>
                      </Card.Text>
                    </Card.Body>
                  </Card>
                </div>
              </Col>
              <Col>
                <Row>
                  {Acutalans[change].answerOptions.map((e) => (
                    <Col sm={12} xl={12} xxl={12} md={12}>
                      <button
                        className="w-75 mt-3 p-1 text-info"
                        style={{
                          border: "none",
                          outline: "none",
                          borderRadius: "10px",
                          backgroundColor: "white",

                          boxShadow: "#2b6777  20px 13px 20px -10px"
                        }}
                        onClick={() => tochange(e.value)}
                      >
                        {e.answerText}
                      </button>
                    </Col>
                  ))}
                </Row>
              </Col>
            </Row>
          </div>
        )}
      </Container>
    </div>
  );
}
