import React from "react";
import { Navbar, Nav, Container } from "react-bootstrap";
import { LinkContainer } from "react-router-bootstrap";

export default function Heading() {
  return (
    <div>
      <Navbar
        collapseOnSelect
        variant="dark"
        expand="md"
        style={{ backgroundColor: "#2b6777" }}
      >
        <Container>
          <LinkContainer to="/covid">
            <div className="d-flex text-white">
              <h3
                style={{ textShadow: "1px 1px 2px blue" }}
                className="mr-2 mt-1"
              >
                <i class="fas fa-shield-alt"></i>
              </h3>
              <Navbar.Brand style={{ textShadow: "1px 1px 2px blue" }}>
                <b>COVID-19</b>
              </Navbar.Brand>
            </div>
          </LinkContainer>

          <Navbar.Toggle aria-controls="basic-navbar-nav" />
          <Navbar.Collapse className="justify-content-end">
            <Nav>
              <LinkContainer to="/covid">
                <Nav.Link>
                  <h5 className="text-white">Covid_Tracker</h5>
                </Nav.Link>
              </LinkContainer>
              <LinkContainer to="/search/date">
                <Nav.Link>
                  <h5 className="text-white">Daily Cases</h5>
                </Nav.Link>
              </LinkContainer>
              <LinkContainer to="/search">
                <Nav.Link>
                  <h5 className="text-white">States Data</h5>
                </Nav.Link>
              </LinkContainer>
              <LinkContainer to="/quiz">
                <Nav.Link>
                  <h5 className="text-white">Quiz</h5>
                </Nav.Link>
              </LinkContainer>
            </Nav>
          </Navbar.Collapse>
        </Container>
      </Navbar>
    </div>
  );
}
